import { followAPI, usersAPI } from '../../api/api';
import { updateObjectInArray } from '../../utils/object-helpers';

const SET_USERS = 'SET-USERS';
const SET_CURRENT_PAGE = 'SET-CURRENT-PAGE';
const SET_TOTAL_COUNT = 'SET-TOTAL-COUNT';
const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const TOGGLE_IS_FETCHING = 'TOGGLE-IS-FETCHING';
const TOGGLE_FOLLOWING_IN_PROGRESS = 'TOGGLE-FOLLOWING-IN-PROGRESS';

let initialState = {
    users: [],
    pageSize: 10,
    totalCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: []
};

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USERS:
            // –∏–∑–Ω–∞—á–∞–ª—å–Ω–æ —é–∑–µ—Ä—ã –ø—É—Å—Ç—ã–µ. –¥–∞–ª–µ–µ —Å —Å–µ—Ä–≤–µ—Ä–∞ –ø–æ–ª—É—á–∞–µ–º –æ—Ç–≤–µ—Ç –∏ –∑–∞–ø–æ–ª–Ω—è–µ–º —Å—Ç–µ–π—Ç
            // —Å–∫–ª–µ–∏–≤–∞–µ–º 2 –º–∞—Å—Å–∏–≤–∞: —Ç–µ, –∫–æ—Ç–æ—Ä—ã–µ –±—ã–ª–∏ –≤ —Å—Ç–µ–π—Ç–µ, –∏ —Ç–µ, –∫–æ—Ç–æ—Ä—ã–µ –±—ã–ª–∏ –≤ —ç–∫—à–µ–Ω–µ
            // return { ...state, users: [ ...state.users, ...action.users ] };

            // –ø–µ—Ä–µ–∑–∞—Ç–∏—Ä–∞–µ–º —é–∑–µ—Ä–æ–≤. —á—Ç–æ–±—ã –ø—Ä–∏ –ø–∞–≥–∏–Ω–∞—Ü–∏ –Ω–µ –¥—É–±–ª–∏—Ä–æ–≤–∞–ª–æ—Å—å –∏ –Ω–µ —Å–∫–ª–µ–∏–≤–∞–ª–æ—Å—å –¥—Ä—É–≥ –ø–æ–¥–¥—Ä—É–≥–æ–º
            return { ...state, users: action.users };
        case SET_CURRENT_PAGE:
            return { ...state, currentPage: action.page };
        case SET_TOTAL_COUNT:
            return { ...state, totalCount: action.count };
        case FOLLOW: {
            let newObjProps = { followed: true };
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, 'id', newObjProps)
            };
        }
        case UNFOLLOW: {
            let newObjProps = { followed: false };
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, 'id', newObjProps)
            };
        }
        case TOGGLE_IS_FETCHING:
            return { ...state, isFetching: action.isFetching };
        case TOGGLE_FOLLOWING_IN_PROGRESS:
            return {
                ...state,
                followingInProgress: action.isFollowingInProgress
                    ? [ ...state.followingInProgress, action.userId ]
                    // —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏—è –≤–µ—Ä–Ω–µ—Ç –Ω–∞–º –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤, –¥–µ—Å—Ç—Ä—É–∫—Ç—É—Ä–∏–∑. –Ω–µ –Ω—É–∂–Ω–∞
                    : state.followingInProgress.filter(id => id !== action.userId)
            };
        default:
            return state;
    }
}

/* Action Creators */

export const setUsers = (users) =>
    ({ type: SET_USERS, users });

export const setCurrentPage = (page) =>
    ({ type: SET_CURRENT_PAGE, page });

export const setTotalCount = (count) =>
    ({ type: SET_TOTAL_COUNT, count });

export const followUser = (userId) =>
    ({ type: FOLLOW, userId });

export const unfollowUser = (userId) =>
    ({ type: UNFOLLOW, userId });

export const toggleIsFetching = (isFetching) =>
    ({ type: TOGGLE_IS_FETCHING, isFetching });

export const toggleFollowingInProgress = (isFollowingInProgress, userId) =>
    ({ type: TOGGLE_FOLLOWING_IN_PROGRESS, isFollowingInProgress, userId });

/* Thunk Creators */

export const requestUsers = (page, pageSize) => {
    // –≤–æ–∑–≤—Ä–∞—â–∞–µ–º thunk. –∑–∞–º—ã–∫–∞–Ω–∏–µ
    return async (dispatch) => {
        dispatch(toggleIsFetching(true));
        dispatch(setCurrentPage(page));

        let data = await usersAPI.getUsers(page, pageSize);

        dispatch(toggleIsFetching(false));
        dispatch(setUsers(data.items));
        dispatch(setTotalCount(data.totalCount));
    }
}

const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
    dispatch(toggleFollowingInProgress(true, userId));

    let data = await apiMethod(userId);

    // —Å–µ—Ä–≤–µ—Ä –ø–æ–¥—Ç–≤–µ—Ä–¥–∏–ª, —á—Ç–æ –ø–æ–¥–ø–∏—Å–∫–∞ –ø—Ä–æ–∏–∑–æ—à–ª–∞
    if (data.resultCode === 0) {
        dispatch(actionCreator(userId))
    }
    dispatch(toggleFollowingInProgress(false, userId));
}

export const follow = (userId) => {
    return async (dispatch) => {
        let apiMethod = followAPI.follow.bind(followAPI);
        followUnfollowFlow(dispatch, userId, apiMethod, followUser);
    }
}

export const unfollow = (userId) => {
    return async (dispatch) => {
        let apiMethod = followAPI.unfollow.bind(followAPI);
        followUnfollowFlow(dispatch, userId, apiMethod, unfollowUser);
    }
}

